import React, { useContext } from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';

import './App.scss';
import axios from './axios-order';
import Layout from './components/Layout/Layout';
import AdminPanel from './../src/containers/AdminPanel/AdminPanel';
import Auth from './components/Auth/Auth';
import { AuthContext } from './components/context/Auth-Context';
import PricePage from './../src/components/PricePage/PricePage';
import Partners from './components/Partners/Partners';
import Slider from './containers/Slider/Slider';

const App = () => {
  axios.interceptors.request.use(req => {
    const token = sessionStorage.getItem('access__token');    
    if(!token) {return req;}  
    req.headers.Authorization = `Bearer ${token}`;
    return req;
  });

  const authContext = useContext(AuthContext); 
  const token = sessionStorage.getItem('access__token');   
  let authAdmin = <Auth/> ;
  if (authContext.isAuth || token) authAdmin = <AdminPanel/>;
 
  return (
    <div className="App">
      <Router>
        <Layout>
          <Switch>
             <Route exact path='/'>
                <Slider/>
                <Partners/>
             </Route>
            <Route  path="/price" component={PricePage}/>
          </Switch>  
        </Layout>
      </Router>
        {/* <Route exact path="/operator" >
          {authAdmin}
        </Route> */}
    </div>
  );
}

export default App;
